'use client'
import {
  CalendarIcon,
  ClockIcon,
  FilmIcon,
  GlobeIcon,
  StarIcon,
  MessageSquareIcon,
  BadgeDollarSignIcon,
  LinkIcon,
  TagIcon,
  ChevronLeft,
  ChevronRight
} from 'lucide-react'
import { useRef, useState } from 'react'

export default async function DetailsPage({ params }) {
  const { type, id } = await params
  const apiKey = process.env.NEXT_PUBLIC_TMDB_API_KEY

  const res = await fetch(`https://api.themoviedb.org/3/${type}/${id}?api_key=${apiKey}`)
  const data = await res.json()

  const [recommendations, castData, providers, reviews] = await Promise.all([
    fetch(`https://api.themoviedb.org/3/${type}/${id}/recommendations?api_key=${apiKey}`).then(r => r.json()),
    fetch(`https://api.themoviedb.org/3/${type}/${id}/credits?api_key=${apiKey}`).then(r => r.json()),
    fetch(`https://api.themoviedb.org/3/${type}/${id}/watch/providers?api_key=${apiKey}`).then(r => r.json()),
    fetch(`https://api.themoviedb.org/3/${type}/${id}/reviews?api_key=${apiKey}`).then(r => r.json())
  ])

  const image = data.poster_path || data.profile_path
  const title = data.title || data.name

  const castRef = useRef()
  const recRef = useRef()
  const [reviewLimit, setReviewLimit] = useState(2)

  const scrollLeft = ref => ref.current.scrollBy({ left: -400, behavior: 'smooth' })
  const scrollRight = ref => ref.current.scrollBy({ left: 400, behavior: 'smooth' })

  return (
    <div className="p-8 max-w-6xl mx-auto text-white">
      {/* Cabecera */}
      <div className="flex flex-col lg:flex-row gap-8 mb-12">
        <div className="w-full lg:w-1/3 max-w-sm flex flex-col gap-4">
          {image && (
            <img src={`https://image.tmdb.org/t/p/w500${image}`} alt={title} className="rounded-lg shadow-lg w-full h-auto object-cover" />
          )}

          {providers?.results?.ES && (
            <div className="bg-gray-900 p-4 rounded-lg shadow-md">
              <h2 className="text-xl font-bold mb-4">Disponible en plataformas</h2>
              <div className="flex flex-wrap gap-3">
                {providers.results.ES.flatrate?.map(p => (
                  <div key={p.provider_id} className="bg-gray-800 p-2 rounded flex items-center gap-2">
                    <img src={`https://image.tmdb.org/t/p/original${p.logo_path}`} alt={p.provider_name} className="w-8 h-8 object-contain" />
                    <span className="text-white text-sm">{p.provider_name}</span>
                  </div>
                ))}
                <a href={`https://www.themoviedb.org/${type}/${id}/watch`} target="_blank" className="text-blue-400 underline text-sm">Ver en JustWatch →</a>
              </div>
            </div>
          )}
        </div>

        <div className="flex-1 flex flex-col gap-6">
          <div>
            <h1 className="text-4xl font-bold mb-2">{title}</h1>
            {data.tagline && <p className="italic text-gray-400 mb-4"><TagIcon className="inline w-4 h-4 mr-1" /> {data.tagline}</p>}
            {data.overview && <p className="text-gray-300 text-lg leading-relaxed"><MessageSquareIcon className="inline w-4 h-4 mr-1 text-gray-400" /> {data.overview}</p>}
          </div>

          <div className="text-sm text-gray-300 bg-gray-900 p-5 rounded-lg shadow-md space-y-1">
            {data.original_title && <p><FilmIcon className="inline w-4 h-4 mr-2 text-blue-400" /> <strong>Título original:</strong> {data.original_title}</p>}
            {data.release_date && <p><CalendarIcon className="inline w-4 h-4 mr-2 text-green-400" /> <strong>Estreno:</strong> {data.release_date}</p>}
            {data.runtime && <p><ClockIcon className="inline w-4 h-4 mr-2 text-yellow-400" /> <strong>Duración:</strong> {data.runtime} min</p>}
            {data.original_language && <p><GlobeIcon className="inline w-4 h-4 mr-2 text-purple-400" /> <strong>Idioma:</strong> {data.original_language}</p>}
            {data.vote_average && <p><StarIcon className="inline w-4 h-4 mr-2 text-yellow-300" /> <strong>Nota media:</strong> {data.vote_average.toFixed(1)}</p>}
            {data.vote_count && <p><StarIcon className="inline w-4 h-4 mr-2 text-yellow-300" /> <strong>Votos:</strong> {data.vote_count}</p>}
            {data.budget > 0 && <p><BadgeDollarSignIcon className="inline w-4 h-4 mr-2 text-green-500" /> <strong>Presupuesto:</strong> ${data.budget.toLocaleString()}</p>}
            {data.revenue > 0 && <p><BadgeDollarSignIcon className="inline w-4 h-4 mr-2 text-green-500" /> <strong>Recaudación:</strong> ${data.revenue.toLocaleString()}</p>}
            {data.homepage && <p><LinkIcon className="inline w-4 h-4 mr-2 text-blue-300" /> <strong>Sitio web:</strong> <a href={data.homepage} target="_blank" className="underline text-blue-400">{data.homepage}</a></p>}
          </div>

          <div className="text-xs bg-gray-900 rounded-lg p-4 shadow-md text-gray-300">
            <h2 className="text-md font-bold mb-2 text-white">Información adicional</h2>
            {data.genres?.length > 0 && <p><strong>Géneros:</strong> {data.genres.map(g => g.name).join(', ')}</p>}
            {data.production_companies?.length > 0 && <p><strong>Productoras:</strong> {data.production_companies.map(pc => pc.name).join(', ')}</p>}
            {data.production_countries?.length > 0 && <p><strong>Países:</strong> {data.production_countries.map(p => p.name).join(', ')}</p>}
            {data.spoken_languages?.length > 0 && <p><strong>Idiomas:</strong> {data.spoken_languages.map(l => l.english_name).join(', ')}</p>}
          </div>
        </div>
      </div>

      {/* Reparto con scroll */}
      {castData?.cast?.length > 0 && (
        <div className="mt-12">
          <h2 className="text-2xl font-bold text-white mb-4">Reparto principal</h2>
          <div className="relative">
            <button onClick={() => scrollLeft(castRef)} className="absolute left-0 top-1/2 -translate-y-1/2 z-10 p-2 bg-gray-800 rounded-full"><ChevronLeft /></button>
            <div ref={castRef} className="flex overflow-x-hidden space-x-4 px-8">
              {castData.cast.slice(0, 20).map(actor => (
                <a key={actor.id} href={`/details/person/${actor.id}`} className="w-36 flex-shrink-0 bg-gray-800 hover:bg-gray-700 p-2 rounded-lg text-center">
                  <img src={`https://image.tmdb.org/t/p/w185${actor.profile_path}`} alt={actor.name} className="w-full h-44 object-cover rounded mb-2" />
                  <p className="font-semibold text-white text-sm truncate">{actor.name}</p>
                  <p className="text-xs text-gray-400 truncate">{actor.character}</p>
                </a>
              ))}
            </div>
            <button onClick={() => scrollRight(castRef)} className="absolute right-0 top-1/2 -translate-y-1/2 z-10 p-2 bg-gray-800 rounded-full"><ChevronRight /></button>
          </div>
        </div>
      )}

      {/* Críticas */}
      {reviews?.results?.length > 0 && (
        <div className="mt-12">
          <h2 className="text-2xl font-bold text-white mb-4">Críticas de usuarios</h2>
          <div className="grid md:grid-cols-2 gap-6">
            {reviews.results.slice(0, reviewLimit).map(review => (
              <div key={review.id} className="bg-gray-800 p-4 rounded-lg">
                <p className="text-white font-semibold">{review.author}</p>
                <p className="text-gray-400 text-sm mb-2">{new Date(review.created_at).toLocaleDateString()}</p>
                <p className="text-gray-300 whitespace-pre-wrap text-sm">{review.content.slice(0, 300)}...</p>
                <a href={`https://www.themoviedb.org/review/${review.id}`} target="_blank" className="text-blue-400 underline text-sm">Leer más →</a>
              </div>
            ))}
          </div>
          {reviewLimit < reviews.results.length && (
            <div className="text-center mt-4">
              <button onClick={() => setReviewLimit(reviewLimit + 2)} className="px-4 py-2 bg-blue-600 hover:bg-blue-700 rounded text-white">Ver más críticas</button>
            </div>
          )}
        </div>
      )}

      {/* Recomendaciones */}
      {recommendations?.results?.length > 0 && (
        <div className="mt-12">
          <h2 className="text-2xl font-bold text-white mb-4">Recomendaciones</h2>
          <div className="relative">
            <button onClick={() => scrollLeft(recRef)} className="absolute left-0 top-1/2 -translate-y-1/2 z-10 p-2 bg-gray-800 rounded-full"><ChevronLeft /></button>
            <div ref={recRef} className="flex overflow-x-hidden space-x-4 px-8">
              {recommendations.results.slice(0, 12).map(rec => (
                <a key={rec.id} href={`/details/${rec.media_type || type}/${rec.id}`} className="w-40 flex-shrink-0 bg-gray-800 hover:bg-gray-700 p-2 rounded-lg">
                  <img src={`https://image.tmdb.org/t/p/w300${rec.poster_path}`} alt={rec.title || rec.name} className="w-full h-60 object-cover rounded mb-2" />
                  <p className="text-sm font-semibold text-white truncate">{rec.title || rec.name}</p>
                  {rec.vote_average && <p className="text-xs text-yellow-400">⭐ {rec.vote_average.toFixed(1)}</p>}
                </a>
              ))}
            </div>
            <button onClick={() => scrollRight(recRef)} className="absolute right-0 top-1/2 -translate-y-1/2 z-10 p-2 bg-gray-800 rounded-full"><ChevronRight /></button>
          </div>
        </div>
      )}
    </div>
  )
}